import WebSocket from 'ws';
import throttle from 'lodash/throttle';

function gdax() {

  this.prices = {
    getAll: async({ currency }) => {
      console.log('> Trying to get Gdax prices for Currency :', currency);
      // Gdax only accepts currency ending with "USD"
      transformCurrency(currency);

      // Initialize server with transformed currency
      const result = await initializeServer(currency);
      console.log('> heres your result:', result);
      return;
    },

  }
}

function initializeServer(currency) {
  const ws = new WebSocket('wss://ws-feed.gdax.com');
  ws.on('open', (() => {
    console.log('> Server has opened. Here is the date now.');
    ws.send(Date.now());
  }));
  ws.on('close', (() => {
    console.log('> Server has closed. Here is the date now in console.', Date.now());
  }));
  /* Runs throttle function every 500 milliseconds */
  ws.on('message', throttle((gdaxData) => {
    console.log('> This is your Gdax Data:', gdaxData);
  }, 500));
}

function transformCurrency(currency) {
  const currencyArray = currency.split('-');
  // If currency does not end with 'USD', add 'USD' to it
  if (currencyArray[1] !== 'USD') {
    return `${currency}-USD`;
  }
  return currency;
}
const Gdax = new gdax();
export default Gdax;



